@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma.min.css);
* {
  box-sizing: border-box;
}

.app{max-width:960px;margin:16px auto 0;padding:16px;border:#e1e1e1 1px solid}

.Loader{display:-webkit-flex;display:flex;width:100%;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.Loader__content{border-radius:50%;width:2em;height:2em;margin:1em auto;border:0.3em solid #ddd;border-left-color:#000;-webkit-animation:load8 1.2s infinite linear;animation:load8 1.2s infinite linear}@-webkit-keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

